.header_outer_container {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  height: 10vh;
  color: white;
  transition: all 0.5s;
  transition-timing-function: ease-in;
  z-index: 1;
  opacity: 0;
  animation: fade-in 3s forwards;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 2;
  }
}
.nav_black {
  background-color: rgb(3, 3, 3);
}
.header_container {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_outer_container a
 {
    text-decoration: none;
    color:white;
}
.header_left ul {
  width: 140%;
  display: flex;
  align-items: center;
  list-style: none;
  list-style: none;
  justify-content: space-between;
  cursor: pointer;
}

.header-right ul {
  width: 140%;
  display: flex;
  align-items: center;
  list-style: none;
  list-style: none;
  justify-content: space-between;
}
.header_icons {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.header_icons li {
  display: inline-block;
  margin-right: 10px;
}
.search-box {
  position: relative;
}

.search-box input[type="text"] {
  width: 0;
  opacity: 0;
  transition: width 0.3s ease, opacity 0.3s ease;
  border: solid 0.1px white;
  background-color: black;
}

.search-box.active input[type="text"] {
  width: 200px; 
  opacity: 1;
}



